import {Injectable} from '@angular/core';

@Injectable()
export class HeaderServiceService {

  private headerValue;

  constructor() {
    this.headerValue = 'LoginHeader';
  }

  setUserLoggedin(headerValue) {
    this.headerValue = headerValue;
  }

  getUserLoggedin() {
    return this.headerValue;
  }
}



